/****** styles ******/
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: #e6e6e6;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #b1b1b1;
}

.dark ::-webkit-scrollbar-thumb {
  background: #383838;
  border-radius: 5px;
}
.dark ::-webkit-scrollbar-thumb:hover {
  background: #4d4d4d;
}
.dark.chat-app {
  background: #1d1d1d;
}
.dark.chat-app .chat {
  border-left-color: #2d2d2d !important;
}
.dark.chat-app .chat-header {
  border-bottom-color: #2d2d2d !important;
}
.dark.chat-app .chat-header .chat-about {
  color: #fff;
}
.dark.chat-app .chat-header .chat-about small {
  color: #999;
}
.dark.chat-app .people-list .search span {
  background: #3a3a3a;
  border-color: #3a3a3a;
}
.dark.chat-app .people-list .search span i.fa {
  color: #7e7e7e;
}
.dark.chat-app .people-list .search input[type=search] {
  background: #2d2d2d;
  border-color: #2d2f31;
  color: #dfdfdf;
}
.dark.chat-app .people-list .chat-list li {
  color: #fff;
}
.dark.chat-app .people-list .chat-list li:hover {
  background: #353535;
  cursor: pointer;
}
.dark.chat-app .people-list .chat-list li.active {
  background: #353535;
  color: #fff;
  font-weight: 500;
}
.dark.chat-app .people-list .chat-list li .status {
  color: #999;
}
.dark.chat-app .chat-history {
  border-bottom-color: #2d2d2d !important;
}
.dark.chat-app .chat-history .message-data-time {
  color: #434651;
}
.dark.chat-app .chat-history .message {
  color: #b4b4b4;
}
.dark.chat-app .chat-history .my-message {
  background: #4d4d4d;
}
.dark.chat-app .chat-history .other-message {
  background: #2b457e;
}
.dark.chat-app .chat-message input {
  background: #2d2d2d;
  border-color: #2d2f31;
  color: #dfdfdf;
}

.card {
  background: #fff;
  transition: 0.5s;
  border: 0;
  border-radius: 0.55rem;
  position: relative;
  width: 100%;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  padding: 1px;
}

.chat-app {
  height: 100%;
}
.chat-app .people-list {
  width: 280px;
  position: absolute;
  left: 0;
  top: 0;
  padding: 20px;
  z-index: 7;
}
.chat-app .chat {
  margin-left: 280px;
  border-left: 1px solid #efefef;
}

.people-list {
  height: 100%;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  overflow: hidden;
}
.people-list .chat-list {
  height: calc(100% - 48px);
  overflow-y: scroll;
}
.people-list .chat-list li {
  padding: 10px 15px;
  list-style: none;
  border-radius: 3px;
  margin-right: 4px;
}
.people-list .chat-list li:not(:last-child) {
  margin-bottom: 4px;
}
.people-list .chat-list li:hover {
  background: #efefef;
  cursor: pointer;
}
.people-list .chat-list li.active {
  background: #bec0c0;
}
.people-list .chat-list li .name {
  font-size: 15px;
}
.people-list .chat-list li img {
  width: 45px;
  border-radius: 50%;
}
.people-list img {
  float: left;
  border-radius: 50%;
}
.people-list .about {
  float: left;
  padding-left: 8px;
}
.people-list .status {
  color: #999;
  font-size: 13px;
}

.chat {
  height: 100%;
}
.chat .chat-header {
  padding: 15px 20px;
  border-bottom: 2px solid #efefef;
}
.chat .chat-header img {
  float: left;
  border-radius: 40px;
  width: 40px;
}
.chat .chat-header .chat-about {
  float: left;
  padding-left: 10px;
}
.chat .chat-history {
  padding: 20px;
  border-bottom: 2px solid #efefef;
  height: calc(100% - 170px);
  overflow-y: auto;
}
.chat .chat-history ul {
  padding: 0;
}
.chat .chat-history ul li {
  list-style: none;
  margin-bottom: 15px;
}
.chat .chat-history ul li:last-child {
  margin-bottom: 0px;
}
.chat .chat-history .message-data {
  margin-bottom: 5px;
}
.chat .chat-history .message-data img {
  border-radius: 40px;
  width: 40px;
}
.chat .chat-history .message-data-time {
  color: #434651;
  padding-left: 6px;
  font-size: 12px;
}
.chat .chat-history .message {
  color: #444444;
  padding: 12px 16px;
  line-height: 26px;
  font-size: 16px;
  border-radius: 5px;
  display: inline-block;
  position: relative;
}
.chat .chat-history .my-message {
  background: #ffffff;
}
.chat .chat-history .other-message {
  background: #e8f1f3;
  text-align: right;
}
.chat .chat-message {
  padding: 20px;
}

.online,
.offline,
.me {
  margin-right: 2px;
  font-size: 8px;
  vertical-align: middle;
}

.online {
  color: #86c541;
}

.offline {
  color: #e47297;
}

.me {
  color: #1d8ecd;
}

.float-right {
  float: right;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

@media only screen and (max-width: 767px) {
  .chat-app .people-list {
    height: 465px;
    width: 100%;
    overflow-x: auto;
    background: #fff;
    left: -400px;
    display: none;
  }

  .chat-app .people-list.open {
    left: 0;
  }

  .chat-app .chat {
    margin: 0;
  }

  .chat-app .chat .chat-header {
    border-radius: 0.55rem 0.55rem 0 0;
  }

  .chat-app .chat-history {
    height: 300px;
    overflow-x: auto;
  }
}
@media only screen and (min-width: 768px) and (max-width: 992px) {
  .chat-app .chat-list {
    height: 650px;
    overflow-x: auto;
  }

  .chat-app .chat-history {
    height: 600px;
    overflow-x: auto;
  }
}
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 1) {
  .chat-app .chat-list {
    height: 480px;
    overflow-x: auto;
  }

  .chat-app .chat-history {
    height: calc(100vh - 350px);
    overflow-x: auto;
  }
}